import { filter, isInteger, toLower } from 'lodash';

type StorageName = "LocalStorage" | "IndexedDBStorage";

export interface StorageInterface {
  /**
   * @param key the key of the data to fetch
   * @param filters the filters to apply to the data {key: value}
   * @returns the data
   * @returns empty array if no data with provided key is found
   */
  getFromStorage(key: string, filters?: Record<string, any>): Promise<any>;

  /**
   * @param key the key of the data to save
   * @param data the data to save
   * @returns true if data is saved
   * @returns false if data is not saved
   * */
  saveToStorage(key: string, data: any): boolean;

  /**
   * @param key the key of the data to delete
   * @returns true if data is deleted
   * @returns false if data is not deleted
   * */
  deleteFromStorage(key: string): boolean;

  /**
   * @param key the key of the data to check
   * @returns true if data exists
   * @returns false if data does not exist
   * */
  existsinStorage(key: string): Promise<any>;
}

export const storageFactory = (storageName: StorageName): StorageInterface => {
  return eval(`new ${storageName}()`) as StorageInterface;
};

export class LocalStorage implements StorageInterface {
  existsinStorage(key: string): Promise<boolean> {
    const data = localStorage.getItem(key);
    if (data) {
      return Promise.resolve(true);
    }
    return Promise.resolve(false);
  }

  getFromStorage(key: string): Promise<any> {
    return new Promise((resolve, reject) => {
      const data = localStorage.getItem(key);
      if (data) {
        return resolve(JSON.parse(data));
      }
      resolve([]);
    });
  }

  deleteFromStorage(key: string): boolean {
    localStorage.removeItem(key);
    return true;
  }

  saveToStorage(key: string, data: JSON): boolean {
    localStorage.setItem(key, JSON.stringify(data));
    return true;
  }
}

export class IndexedDBStorage implements StorageInterface {
  db!: IDBDatabase;

  constructor() {
    const openRequest = indexedDB.open("offline_db", 1);
    openRequest.onupgradeneeded = function () {
      openRequest.result.createObjectStore("openmrs");
    };
    openRequest.onsuccess = () => {
      this.db = openRequest.result;
    };
  }

  existsinStorage(key: string): Promise<unknown> {
    return new Promise((resolve, reject) => {
      const request = this.db
        .transaction("openmrs")
        .objectStore("openmrs")
        .get(key);
      request.onsuccess = () => {
        resolve(request.result);
      };
      request.onerror = () => {
        reject(request.error);
      };
    });
  }

  getFromStorage(key: string, filters: any): Promise<any> {
    return new Promise((resolve, reject) => {
      const request = this.db
        .transaction("openmrs")
        .objectStore("openmrs")
        .openCursor();
      request.onsuccess = () => {
        const cursor = request.result;
        if (cursor) {
          if (cursor.key === key) {
            if (filters) {
              resolve(filteredData(cursor, filters));
            } else {
              resolve(cursor.value.data);
            }
          }
          cursor.continue();
        } else {
          resolve([]);
        }
      };
      request.onerror = () => {
        reject("error");
      };
    });
  }

  saveToStorage(key: string, data: any): boolean {
    this.db
      .transaction("openmrs", "readwrite")
      .objectStore("openmrs")
      .put(data, key);
    return true;
  }

  deleteFromStorage(key: string): boolean {
    this.db
      .transaction("openmrs", "readwrite")
      .objectStore("openmrs")
      .delete(key);
    return true;
  }
}

function filteredData(cursor: IDBCursorWithValue, filters: any): any {
  return filter(cursor.value, (item: any) => {
    return Object.entries(filters).every(([key, value]: any) => {
      if (isInteger(item[key])) {
        return !value || item[key] === value;
      } else {
        return !value || toLower(item[key]).includes(toLower(value));
      }
    });
  });
}

